import { Input } from "components/input/input";
import { Label } from "components/label/label";
import { FormField } from "models/form-field";
import { InputType } from "models/input-type";
import React, { Dispatch, SetStateAction } from "react";
import style from "./style.module.css";


export type FormFieldsProps = {
    fields: Readonly<FormField[]>,
    selectedFields: FormField[],
    setSelectedFields: Dispatch<SetStateAction<FormField[]>>,
};

export const FormFields = (props: FormFieldsProps) =>
    <>
        {
            props.fields.map((field) =>
                <Label key={field.name} className={style.label}>
                    <Input
                        defaultChecked={field.required}
                        disabled={field.required}
                        name={field.name}
                        onChange={
                            (e) => e.currentTarget.checked
                                ? props.setSelectedFields([...props.selectedFields, field])
                                : props.setSelectedFields(removeField(props.selectedFields, field))
                        }
                        type="checkbox"
                    />
                    <span className={style.labelText}>{field.label}</span>
                </Label>
            )
        }
    </>;

function removeField(selectedFields: FormField[], field: FormField): FormField[] {
    return selectedFields.filter((selectedField) => selectedField.name !== field.name);
}
