import { InputType } from "models/input-type";
import React from "react";
import { Calendar, Type } from "react-feather";
import style from "./style.module.css";


export type TextInputProps = {
    label: string,
    type: InputType,
};

export const TextInput = (props: TextInputProps) =>
    <>
        <div className={style.container}>
            <p className={style.label}>{props.label}</p>
            <div className={style.fakeInput}>
                {getIcon(props.type)}
            </div>
        </div>
    </>;

function getIcon(type: InputType) {
    switch (type) {
        case InputType.TEXT:
            return <Type className={style.icon} />;
        case InputType.DATE:
            return <Calendar className={style.icon} />
    }
}