import { Select } from './components/select/select';
import { FormFields } from 'components/form-fields/form-fields';
import { FormVisualizer } from 'components/form-visualizer/form-visualizer';
import React, { useMemo, useState } from 'react';
import { CompanyName, FormType } from 'models/company';
import style from './App.module.css';
import { FormConfig } from 'config/form.config';
import { FormCode } from 'components/form-code/form-code';
import { SerializedForm } from 'models/serialized-form';
import { FormStyle } from 'components/form-style/form-style';
import { FormPreview } from 'components/form-preview/form-preview';


function App() {
  const [company, setCompany] = useState(CompanyName.HOSTAWAY);
  const [formType, setFormType] = useState(FormType.INQUIRE);
  const [selectedFields, setSelectedFields] = useState(FormConfig[company][formType].filter((field) => field.required));
  const [serializedForm, setSerializedForm] = useState({cssClasses: [], fields: []} as SerializedForm);
  const [isCode, setIsCode] = useState(true);

  const fields = useMemo(() => FormConfig[company][formType], [company, formType]);

  return (
    <div className={style.appView}>
      <div className={style.appViewHeader}>
        <div className={style.appViewHeaderItem}>
          <span>Select Company To Integrate With:</span>
          <Select
            defaultValue={company}
            onChange={(e) => setCompany(e.currentTarget.value as CompanyName)}
            options={Object.values(CompanyName).map((companyName) => ({
              label: companyName,
              value: companyName,
            }))} />
        </div>
        <div className={style.appViewHeaderItem}>
          <span>Select Form Type</span>
          <Select
            defaultValue={formType}
            onChange={(e) => setFormType(e.currentTarget.value as FormType)}
            options={Object.values(FormType).map((formType) => ({
              label: formType,
              value: formType,
            }))} />
        </div>
      </div>
      <div className={style.appViewFormEditor}>
        <div className={style.appViewForm}>
          <FormVisualizer
            fields={selectedFields}
            serializedForm={serializedForm}
            setFieldsInOrder={setSelectedFields}
            setSerializedForm={setSerializedForm} />
            {
              isCode
                ? <FormCode serializedForm={serializedForm} showCode={setIsCode} />
                : <FormPreview
                    serializedForm={serializedForm}
                    showCode={setIsCode} />
            }
        </div>
        <div className={style.rightPanelDivider} />
        <div className={style.appViewRightPanel}>
          <div className={style.appViewFields}>
            <FormFields
              fields={fields}
              selectedFields={selectedFields}
              setSelectedFields={setSelectedFields}/>
          </div>
          <FormStyle />
        </div>
      </div>
      <div className={style.appViewFooter}>

      </div>
    </div>
  );
}

export default App;
