import { DragItem } from "models/drag-item";
import { DragItemType } from "models/drag-item-type";
import { FormField } from "models/form-field";
import {MoreVertical} from "react-feather";
import React, { ReactNode, useRef } from "react";
import type { Identifier, XYCoord } from 'dnd-core';
import { useDrag, useDrop } from 'react-dnd';
import style from "./style.module.css";


export type DraggableFieldProps = FormField & DragItem & {
  children: ReactNode,
  onDrop: () => void
};

export const DraggableField = (props: DraggableFieldProps) => {
    const ref = useRef<HTMLDivElement>(null);
    const [{ handlerId }, drop] = useDrop<
        DragItem,
        void,
        { handlerId: Identifier | null }
    >({
        accept: DragItemType.FIELD,
        collect(monitor) {
            return {
                handlerId: monitor.getHandlerId()
            };
        },
        hover(item: DragItem, monitor) {
            if (!ref.current) {
                return
              }
              const dragIndex = item.index
              const hoverIndex = props.index;
              // Don't replace items with themselves
              if (dragIndex === hoverIndex) {
                return
              }

              // Determine rectangle on screen
              const hoverBoundingRect = ref.current?.getBoundingClientRect()

              // Get vertical middle
              const hoverMiddleY =
                (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2

              // Determine mouse position
              const clientOffset = monitor.getClientOffset()

              // Get pixels to the top
              const hoverClientY = (clientOffset as XYCoord).y - hoverBoundingRect.top

              // Only perform the move when the mouse has crossed half of the items height
              // When dragging downwards, only move when the cursor is below 50%
              // When dragging upwards, only move when the cursor is above 50%

              // Dragging downwards
              if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
                return;
              }

              // Dragging upwards
              if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
                return;
              }

              // Time to actually perform the action
              props.moveField(dragIndex, hoverIndex)

              // Note: we're mutating the monitor item here!
              // Generally it's better to avoid mutations,
              // but it's good here for the sake of performance
              // to avoid expensive index searches.
              item.index = hoverIndex
        },
    });

    const [{ isDragging }, drag] = useDrag({
        type: DragItemType.FIELD,
        item: () => {
          return { id: props.id, index: props.index };
        },
        collect: (monitor: any) => ({
          isDragging: monitor.isDragging(),
        }),
        end: (field, monitor) => {
          const dropResult = monitor.getDropResult<{name: string}>()
          if (field && dropResult) {
            props.onDrop();
          }
        },
    });

    const opacity = isDragging ? 0 : 1;

    drag(drop(ref));

    return (
        <div
            className={style.draggable}
            data-handler-id={handlerId}
            ref={ref}
            style={{ opacity }}>
            <MoreVertical className={style.dragIcon} size={16} />
            <MoreVertical className={`
              ${style.dragIcon}
              ${style.dragIcon2}
            `} size={16} />
            {props.children}
        </div>
    );
};