import { DndProvider } from 'react-dnd'
import { DraggableField } from './components/draggable-field/draggable-field';
import { FormField } from 'models/form-field';
import { HTML5Backend } from 'react-dnd-html5-backend'
import { InputType } from 'models/input-type';
import { SerializedForm } from 'models/serialized-form';
import { SerializedFormField } from 'models/serialized-form-field';
import { TextInput } from './components';
import update from 'immutability-helper';
import React, { Dispatch, SetStateAction, useCallback, useEffect, useState } from 'react';
import style from 'style.module.css';


export type FormVisualizerProps = {
    fields: FormField[],
    serializedForm: SerializedForm,
    setFieldsInOrder: Dispatch<SetStateAction<FormField[]>>,
    setSerializedForm: Dispatch<SetStateAction<SerializedForm>>,
};

export const FormVisualizer = (props: FormVisualizerProps) => {
    const [serializedFields, setSerializedFields] = useState([] as SerializedFormField[]);

    const moveField = useCallback((dragIndex: number, hoverIndex: number) =>
        props.setFieldsInOrder((previousOrder: FormField[]) =>
            update(previousOrder, {
                $splice: [
                    [dragIndex, 1],
                    [hoverIndex, 0, previousOrder[dragIndex] as FormField],
                ],
            })
        )
    , []);
    const renderField = useCallback((field, index, fields) =>
        <div key={index}>
            <DraggableField
                {...field}
                index={index}
                moveField={moveField}
                onDrop={() =>
                    props.setSerializedForm({
                        ...props.serializedForm,
                        fields: fields as SerializedFormField[],
                    })
                }>
                {getFieldComponent(field)}
            </DraggableField>
        </div>
    , []);

    useEffect(() => {
        props.setSerializedForm({
            ...props.serializedForm,
            fields: props.fields as SerializedFormField[],
        });
    }, [props.fields]);
    return <>
        <DndProvider backend={HTML5Backend}>
            {
                props.fields.map((field, index, self) => renderField(field, index, self))
            }
        </DndProvider>
    </>
};

function getFieldComponent(field: FormField) {
    switch (field.type) {
        case InputType.DATE:
        case InputType.TEXT:
            return <TextInput label={field.label} type={field.type} />
    }
}
