import { CopyToClipboard } from 'react-copy-to-clipboard';
import { InputType } from "models/input-type";
import { SerializedForm} from "models/serialized-form";
import React, { Dispatch, SetStateAction } from "react";
import { Clipboard, Eye } from "react-feather";
import style from "./style.module.css";
import { SerializedFormField } from 'models/serialized-form-field';


export type FormCodeProps = {
    serializedForm: SerializedForm
    showCode: Dispatch<SetStateAction<boolean>>
};

export const FormCode = (props: FormCodeProps) =>
    <>
        <pre className={style.code}>
            <div className={style.iconWrapper}>
                <CopyToClipboard
                    onCopy={() => alert("Form Code Copied To Clipboard")}
                    text={getText(props.serializedForm.fields)}>
                    <button className={style.copyButton}><Clipboard className={`${style.icon} ${style.clipboardIcon}`} /></button>
                </CopyToClipboard>
                <Eye className={`${style.icon} ${style.previewIcon}`} onClick={() => props.showCode(false)} />
            </div>
            <span>{"<form>"}</span>
            {props.serializedForm.fields.map((field) =>
                <span className={style.indent} key={field.name}>
                    {
                        `<label for="${field.name}">`
                    }
                    <span className={style.indent}>{`<span>${field.label}</span>`}</span>
                    <span className={style.indent}>
                        {
                            field.type === InputType.TEXTAREA
                                ? `<textarea name="${field.name}" ${field.required ? "required" : ""}></textarea>`
                                : `<input name="${field.name}" type="${field.type}" ${field.required ? "required" : ""} />`
                        }
                    </span>
                    {`</label>`}
                </span>
            )}
            <span>{"</form>"}</span>
        </pre>
    </>

function getText(fields: SerializedFormField[]): string {
    let result = `<form>`;

    fields.forEach((field) => {
        result += `<label for="${field.name}">`;
        result += `<span>${field.label}</span>`;
        result += field.type === InputType.TEXTAREA
            ? `<textarea name="${field.name}" ${field.required ? "required" : ""}></textarea>`
            : `<input name="${field.name}" type="${field.type}" ${field.required ? "required" : ""} />`;
        result += '</label>';
        result += '</form>';
    });

    return result;
}
