import { Input } from 'components/input/input';
import { Label } from 'components/label/label';
import { InputType } from 'models/input-type';
import { SerializedForm } from 'models/serialized-form';
import React, { Dispatch, SetStateAction } from 'react';
import { Code } from 'react-feather';
import style from './style.module.css';


export type FormPreviewProps = {
    showCode: Dispatch<SetStateAction<boolean>>,
    serializedForm: SerializedForm,
};

export const FormPreview = (props: FormPreviewProps) =>
    <>
        <div className={style.preview}>
            <div className={style.iconWrapper}>
                <span>Live Form Preview</span>
                <Code className={`${style.icon} ${style.codeIcon}`} onClick={() => props.showCode(true)} />
            </div>
            <form>
                {
                    props.serializedForm.fields.map((field) =>
                        <Label htmlFor={field.name} key={field.label} className={style.formField}>
                            <span>{field.label}</span>
                            {
                                field.type === InputType.TEXTAREA
                                    ? <textarea name={field.name}></textarea>
                                    : <Input name={field.name} type={field.type} />
                            }
                        </Label>
                    )
                }
            </form>
        </div>
    </>;
